}
@font-face {
  font-family: "Oswald";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
