.advantages__item {
  position: relative;
  margin-bottom: 26px;
  font-size: 14px;
  line-height: 1.286em;
}
.advantages__item::before {
  content: "";
  position: absolute;
  left: -80px;
  top: -1px;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-position: center;
  background-size: 50px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .advantages__item {
    width: 245px;
    margin-bottom: 50px;
    padding-top: 145px;
    padding-bottom: 50px;
    font-size: 16px;
    line-height: 1.5em;
  }
  .advantages__item::before {
    left: 0;
    top: 33px;
    width: 80px;
    height: 80px;
  }
  .advantages__item::after {
    counter-increment: number;
    content: counter(number) "";
    position: absolute;
    top: -5px;
    left: 116px;
    z-index: -1;
    font-family: "Oswald", sans-serif;
    color: #f2f2f2;
    font-size: 281px;
    line-height: 1em;
  }
}
@media (min-width: 1440px) {
