  line-height: 1.43em;
  font-style: normal;
}
@media (min-width: 768px) {
  .contact__address {
    width: 165px;
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.25em;
    text-align: right;
  }
}
@media (min-width: 1440px) {
  .contact__address {
    width: 160px;
    text-align: left;
  }
}
.contact__address-city {
  display: block;
  margin-top: 2px;
}
@media (min-width: 768px) {
  .contact__address-city {
    margin-top: 10px;
    font-size: 16px;
    line-height: 1.25em;
    text-align: right;
  }
}
@media (min-width: 1440px) {
  .contact__address-city {
    text-align: left;
  }
}
